<template>
  <div>
    <Spinner v-if="isLoading"/>
    <form v-else @submit.stop.prevent="handleSubmit">
      <div class="form-group">
        <label for="name">Name</label>
        <input
          v-model="restaurant.name"
          id="name"
          type="text"
          class="form-control"
          name="name"
          placeholder="Enter name"
          required
        >
      </div>

      <div class="form-group">
        <label for="categoryId">Category</label>
        <select
          id="categoryId"
          v-model="restaurant.id"
          class="form-control"
          name="categoryId"
          required
        >
          <option
            value=""
            selected
            disabled
          >
            --請選擇--
          </option>
          <option v-for="category in categories" :key="category.id" :value="category.id">
            {{ category.name }}
          </option>
        </select>
      </div>

      <div class="form-group">
        <label for="tel">Tel</label>
        <input
          id="tel"
          v-model="restaurant.tel"
          type="text"
          class="form-control"
          name="tel"
          placeholder="Enter telephone number"
        >
      </div>

      <div class="form-group">
        <label for="address">Address</label>
        <input
          id="address"
          v-model="restaurant.address"
          type="text"
          class="form-control"
          placeholder="Enter address"
          name="address"
        >
      </div>

      <div class="form-group">
        <label for="opening-hours">Opening Hours</label>
        <input
          id="opening-hours"
          v-model="restaurant.openingHours"
          type="time"
          class="form-control"
          name="opening_hours"
        >
      </div>

      <div class="form-group">
        <label for="description">Description</label>
        <textarea
          id="description"
          v-model="restaurant.description"
          class="form-control"
          rows="3"
          name="description"
        />
      </div>

      <div class="form-group">
        <label for="image">Image</label>
        <img
          v-if="restaurant.image"
          :src="restaurant.image"
          class="d-block img-thumbnail mb-3"
          width="200"
          height="200"
        >
        <input
          id="image"
          type="file"
          name="image"
          accept="image/*"
          class="form-control-file"
          @change="handleFileChange"
        >
      </div>

      <button
        type="submit"
        class="btn btn-primary"
        :disabled="isProcessing"
      >
        {{ isProcessing ? '處理中...' : '送出' }}
      </button>
    </form>
  </div>
</template>

<script>
import adminAPI from '../apis/admin'
import { Toast } from '../utils/helpers'
import Spinner from '../components/Spinner.vue'

export default {
  name: 'AdminRestaurantForm',
  components: {
    Spinner
  },
  props: {
    initialRestaurant: {
      type: Object,
      default: () => ({
        name: '',
        categoryId: '',
        tel: '',
        address: '',
        description: '',
        image: '',
        openingHours: '',
      })
    },
    isProcessing: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      // 當為edit的頁面時，initialRestaurant有值則放入restaurant中
      // 若為new的頁面時，initialRestaurant為default值
      restaurant: {
        ...this.initialRestaurant
      },
      categories: [],
      isLoading: true
    }
  },
  created () {
    this.fetchCategories()
  },
  watch: {
    // 監控當餐廳資料從AdminRestaurantEdit取回之後，再放入表單中
    // 避免還未取得資料的時候，就已經渲染完表單
    initialRestaurant(newValue) {
      this.restaurant = {
        ...this.restaurant,
        ...newValue
      }
      // this.restaurant = this.initialRestaurant
    }
  },
  methods: {
    // 向伺服器取得餐廳類別的函式
    async fetchCategories () {
      try {
        // 解構賦值將餐廳類別取出
        const { data } = await adminAPI.categories.get()

        this.categories = data.categories

        // 利用isLoading控制表單顯示與否
        // 只有在餐廳類別從伺服器取得資料後，才顯示表單
        // 就不會造成表單已經渲染完成，但是餐廳類別還沒取得的狀況
        this.isLoading = false
      } catch(error) {
        this.isLoading = false
        Toast.fire({
          icon: 'error',
          title: '無法取得餐廳類別，請稍後再試'
        })
      }
      
    },
    handleFileChange (e) {
      // 利用解構賦值將files選出
      const { files } = e.target
      
      if (files.length === 0) {
        // 使用者沒有選擇上傳的檔案
        this.restaurant.image = ''
      } else {
        // 否則產生預覽圖
        const imageURL = window.URL.createObjectURL(files[0])
        this.restaurant.image = imageURL
      }
    },
    handleSubmit(e) {
      // 當使用者未填餐廳名稱時顯示的訊息
      if (!this.restaurant.name) {
        Toast.fire({
          icon: 'warning',
          title: '請填寫餐廳名稱'
        })
        return
        // 當使用者未填餐廳類別時顯示的訊息
      } else if (!this.restaurant.categoryId) {
        Toast.fire({
          icon: 'warning',
          title: '請選擇餐廳類別'
        })
        return
      }

      // 將form表單選出
      const form = e.target
      // 將form表單轉成FormData的格式
      const formData = new FormData(form)
      // 利用emit將after-submit事件傳遞至父元件
      this.$emit('after-submit', formData)
    },
    findCategoryName() {
      this.categories.filter( category => {
        category.id === this.restaurant.categoryId
      })
    }
  }
}
</script>